@import url('https://fonts.googleapis.com/css2?family=Sansita&display=swap');


.container1{
    display: flex;
    height: 100vh;
    background-color: #fff;
    background-image: url("../../resources/paw-1345885_640.png");
    background-size: 200px;
    background-repeat: repeat;
    opacity: 90%;
    animation: fadein 0.3s;
    transition: all 0.2ms;
}

@keyframes fadein {
    0% {
    opacity:0;
    }
    10%{
        opacity: 0.1;
    }
    20%{
        opacity: 0.2;
    }
    30%{
        opacity: 0.3;
    }
    40%{
        opacity: 0.4;
    }
    50%{
        opacity:0.5;
    }
    60%{
        opacity: 0.6;
    }
    70%{
        opacity: 0.7;
    }
    80%{
        opacity: 0.8;
    }
    90%{
        opacity: 0.9;
    }
    100% {
        opacity:1;
    }
}

.container2{
    height: 90vh;
    width: 90%;
    max-width: 1600px;
    margin: auto;
    box-shadow: 0px 10px 10px black;
    display: grid;
    overflow: hidden;
    background-color: #f7f5e4;
    animation: fadein 0.3s;
    background-image: url("../../resources/dog-4450311_1280.png");
    background-repeat: no-repeat;
    background-position: left;
    background-size: 700px;
}

.container3{
    font-family: 'Sansita', sans-serif;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding-right: 20%;
    padding-bottom: 15%;
}

.container4{

}

.titleC{
    font-size: 3.2rem;
}

.form{

}

.input {
    line-height: 28px;
    border: 2px solid transparent;
    border-bottom-color: #777;
    padding: .2rem 0;
    outline: none;
    background-color: transparent;
    color: #0d0c22;
    transition: .3s cubic-bezier(0.645, 0.045, 0.355, 1);
   }
   
   .input:focus, .input:hover {
    outline: none;
    padding: .2rem 1rem;
    border-radius: 1rem;
    border-color: #7a9cc6;
   }
   
   .input::placeholder {
    color: #777;
   }
   
   .input:focus::placeholder {
    opacity: 0;
    transition: opacity .3s;
   }

   .homeT{
    padding-top: 25px;
   }

.labels{
    font-size: 1.8rem;
}

.errors{
    color: red;
}

.deleted{
    display: inline-block;
    padding-right: 10px;
    font-size: 1.4rem;
    /* flex-direction: row; */
}

.xDel{
    background-color: transparent;
    border: none;
    cursor: pointer;
    color: red;
    font-size: 1rem;
}

.buttonsContainer{

}

.buttons {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 10px;
    font-family: 'Sansita', sans-serif;
    /* display: flex; */
    /* flex-direction: row;
    justify-content: center;
    align-items: center; */
    padding: 10px 28px;
    border-radius: 11px;
    border: 1px solid transparent;
    color: #FFFFFF;
    background-color: #1abc9c;
    font-size: 16px;
    letter-spacing: 1px;
    transition: all 0.15s linear;
    cursor: pointer;
   }
   
   .buttons:hover {
    background-color: rgba(29, 201, 160, 0.08);
    border-color: #1DC9A0;
    color: #1DC9A0;
    transform: translateY(-5px) scale(1.05);
   }
   
   .buttons:active {
    background-color: transparent;
    border-color: #1DC9A0;
    color: #1DC9A0;
    transform: translateY(5px) scale(0.95);
   }
   
   .buttons:disabled {
    background-color: rgba(255, 255, 255, 0.16);
    color: #8E8E93;
    border-color: #8E8E93;
   }
